    "about": "Tipica cucina Piemontese, Fratelli Bruzzone a pochi mesi dall'apertura è stato premiato per i migliori agnolotti di Torino da La Stampa. Nata come gastronomia ora piccola trattoria nel cuore di Torino mantiene vive le sue radici prestando attività di asporto dei propri piatti. Produzione artigianale, farine macinate a pietra, carni piemontesi, verdure a km0 e vini selezionatissimi sono l'animo dell'attività.",
