import { Component, OnInit } from '@angular/core';
import { UtilityService } from '../utility.service';
import { HttpClient } from '@angular/common/http';


@Component({
  selector: 'app-restaurant',
  templateUrl: './restaurant.component.html',
  styleUrls: ['./restaurant.component.css']
})
export class RestaurantComponent implements OnInit {
  


  constructor(
    private utility : UtilityService,
    private http : HttpClient
  ) { }

  sliced=[];
  ngOnInit(): void {
    this.sliced=this.utility.sliced
    this.http.get("https://api.pasto.app/api/v2/utils/724/partners.json").subscribe( (response:any) =>{
        this.sliced = response.slice(0,8)
    }, (error)=>{
        alert("Errore del server")
    })
    /*this.utility.get("724/partners.json").subscribe((response:any) =>{
      this.sliced = response.slice(0,8)
    }, (error)=>{
      alert("Errore del server")
    })
    */
  }

}
